<template>
  <div class="carousel">
    <!-- 轮播图内容 -->
    <div class="carousel-container">
      <div
        v-for="(item, index) in items"
        :key="index"
        class="carousel-item"
        :class="{ active: index === currentIndex }"
        :style="getItemStyle(index)"
        @click="setCurrentIndex(index)"
      >
        <img
          :src="item.image"
          :alt="item.description"
          class="carousel-image"
        />
        <!-- <div class="carousel-description"></div> -->
      </div>
    </div>

    <!-- 控制按钮 -->
    <!-- <div class="carousel-controls">
      <button @click="prev">上一张</button>
      <button @click="next">下一张</button>
    </div> -->
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentIndex: 1, // 从复制的第一张开始
      items: [
        // 复制最后一张到开头
        { image: require('@/assets/公司荣誉/WechatIMG38.jpeg'), description: '鄂州花湖机场' },
        // 原始数据
        { image: require('@/assets/公司荣誉/WechatIMG25.jpeg'), description: '咸九高速' },
        { image: require('@/assets/公司荣誉/WechatIMG24.jpeg'), description: '松枝长江大桥' },
        { image: require('@/assets/公司荣誉/WechatIMG26.jpeg'), description: '武汉天河机场' },
        { image: require('@/assets/公司荣誉/WechatIMG35.jpeg'), description: '深圳地铁13号线' },
        { image: require('@/assets/公司荣誉/WechatIMG36.jpeg'), description: '燕矶长江大桥' },
        { image: require('@/assets/公司荣誉/WechatIMG37.jpeg'), description: '鄂州花湖机场' },
        { image: require('@/assets/公司荣誉/WechatIMG38.jpeg'), description: '鄂州花湖机场' },
        // 复制第一张到结尾
        { image: require('@/assets/公司荣誉/WechatIMG25.jpeg'), description: '咸九高速' }
      ]
    }
  },
  methods: {
    prev () {
      this.currentIndex = (this.currentIndex - 1 + this.items.length) % this.items.length
      this.checkBoundary()
    },
    next () {
      this.currentIndex = (this.currentIndex + 1) % this.items.length
      this.checkBoundary()
    },
    setCurrentIndex (index) {
      this.currentIndex = index
      this.checkBoundary()
    },
    checkBoundary () {
      // 如果到达复制的第一张，跳转到真实的第一张
      if (this.currentIndex === 0) {
        setTimeout(() => {
          this.currentIndex = this.items.length - 2 // 跳转到倒数第二张（真实最后一张）
        }, 0)
      }
      // 如果到达复制的最后一张，跳转到真实的最后一张
      if (this.currentIndex === this.items.length - 1) {
        setTimeout(() => {
          this.currentIndex = 1 // 跳转到第二张（真实第一张）
        }, 0)
      }
    },
    getItemStyle (index) {
      const offset = index - this.currentIndex // 计算当前 Slide 与激活 Slide 的偏移量
      const scale = index === this.currentIndex ? 1 : 0.8 // 激活的 Slide 放大，其他缩小
      const translateX = offset * 100 // 根据偏移量调整位置
      const zIndex = index === this.currentIndex ? 1 : 0 // 激活的 Slide 在最上层

      return {
        transform: `translateX(${translateX}%) scale(${scale})`,
        zIndex: zIndex
      }
    }
  },
  mounted () {
    // 自动播放
    setInterval(this.next, 3000)
  }
}
</script>

<style>
.carousel {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
}

.carousel-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 400px;
}

.carousel-item {
  position: absolute;
  width: 60%;
  height: 80%;
  transition: all 0.5s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.carousel-item.active {
  width: 80%;
  height: 100%;
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* 确保图片完整显示 */
}

.carousel-description {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: white;
  font-size: 1.2em;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5px 10px;
  border-radius: 5px;
}

.carousel-controls {
  margin-top: 20px;
  text-align: center;
}

.carousel-controls button {
  margin: 0 5px;
  padding: 10px 20px;
  font-size: 1em;
  cursor: pointer;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
}

.carousel-controls button:hover {
  background-color: #0056b3;
}
</style>
