<template>
  <div class="nav">
    <!-- 左侧 Logo -->
    <div class="left" @click="navigateTo('/home')">
      <img src="@/assets/logo.png" alt="Logo">
    </div>

    <!-- 中间标题 -->
    <div class="title">江城子建材</div>

    <!-- 右侧内容 -->
    <div class="right">
      <button @click="openNav"><van-icon size="2rem" name="wap-nav"></van-icon></button>
    </div>

    <!-- 遮罩层 -->
    <transition name="fade">
      <div v-if="showNav" class="overlay" @click="closeNav"></div>
    </transition>

    <!-- 右侧导航栏 -->
    <transition name="slide">
      <div v-if="showNav" class="sidebar">
        <!-- 一级导航栏 -->
        <div v-if="!showSubNav" class="sidebar-content">
          <div class="sidebar-header">
            <button @click="closeNav"><van-icon name="cross" size="2rem"></van-icon></button>
          </div>
          <ul>
            <li @click="navigateTo('/home')"> <van-icon name="wap-home-o" size="2rem"></van-icon> <p>首页</p> </li>
            <li @click="openSubNav('about')"> <van-icon name="newspaper-o" size="2rem"></van-icon> <p>关于公司</p></li>
            <li @click="navigateTo('/product')"><van-icon name="setting-o" size="2rem"></van-icon> <p>产品中心</p></li>
            <li @click="navigateTo('/engineering')"> <van-icon class-prefix="my-icon" name="custom-logo"></van-icon> <p>工程案例</p></li>
            <li @click="navigateTo('/product')"><van-icon name="completed-o" size="2rem"></van-icon> <p>技术服务</p></li>
            <li @click="navigateTo('/honour')"><van-icon name="medal-o" size="2rem"></van-icon> <p>企业证书</p></li>
            <li @click="navigateTo('/contact')"><van-icon name="service-o" size="2rem"></van-icon> <p>联系我们</p></li>
          </ul>
        </div>

        <!-- 二级导航栏 -->
        <div v-if="showSubNav" class="sidebar-content">
          <div class="sidebar-header">
            <button @click="backToMainNav">返回</button>
          </div>
          <ul>
            <li v-for="item in subNavItems" :key="item.name" @click="handleSubNavClick(item)">
              {{ item.name }}
            </li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'TopNav',
  data () {
    return {
      showNav: false, // 控制导航栏显示与隐藏
      showSubNav: false, // 控制二级导航栏显示与隐藏
      subNavItems: [], // 二级导航栏的内容
      subNavMap: {
        about: [
          { name: '江城子简介', path: '/introduce' },
          { name: '发展历程', path: '/development' },
          { name: '领导致辞', path: '/company' }
        ]
      }
    }
  },
  methods: {
    // 打开导航栏
    openNav () {
      this.showNav = true
      this.showSubNav = false // 确保打开时显示一级导航栏
      document.body.style.overflow = 'hidden' // 禁用页面滚动
    },
    // 关闭导航栏
    closeNav () {
      this.showNav = false
      this.showSubNav = false
      document.body.style.overflow = 'auto' // 启用页面滚动
    },
    // 打开二级导航栏
    openSubNav (key) {
      this.subNavItems = this.subNavMap[key] // 设置二级导航栏内容
      this.showSubNav = true // 显示二级导航栏
    },
    // 返回一级导航栏
    backToMainNav () {
      this.showSubNav = false // 隐藏二级导航栏，显示一级导航栏
    },
    // 处理二级导航栏点击
    handleSubNavClick (item) {
      this.navigateTo(item.path) // 跳转到对应的路由
      this.closeNav() // 点击后关闭导航栏
    },
    // 跳转到指定路由
    navigateTo (path) {
      if (this.$route.path !== path) {
        this.$router.push(path)
      }
      this.closeNav()
    }
  }
}
</script>

<style lang="less" scoped>

.my-icon-custom-logo {
  background-image: url('@/assets/engeering-8.png');
  background-size: contain;
  background-repeat: no-repeat;
  width: 2rem; /* 调整为 2rem */
  height: 2rem; /* 调整为 2rem */
  display: inline-block;
}

.nav {
  display: flex;
  position: fixed;
  width: 100%;
  height: 56px;
  padding: 5px 0;
  color: #303c42;
  align-items: center;
  background-color: white; /* 确保 nav 有背景色，避免内容透过来 */
  z-index: 10; /* 确保 nav 在最上层 */
}

.left {
  flex: 1;
  display: flex;
}

.left img {
  height: 50px;
}

.title {
  display: flex;
  align-items: center;
  width: 200px;
  justify-content: center;
  font-weight: bold;
  font-size: 25px;
}

.right {
  flex: 1;
  text-align: right;
  padding-right: 20px;
}

.right button {
  background-color: rgba(0, 77, 160, 1);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 200px;
  height: 100%;
  background-color: white;
  z-index: 1001;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar-header {
  padding: 10px;
  text-align: right;
}

.sidebar-header button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.sidebar-content ul {
  list-style: none;
  padding: 0;
}

.sidebar-content ul li {
  width: 100%;
display: flex;
align-items: center;
  font-size: 17px;
  cursor: pointer;
  padding: 20px;
}

.sidebar-content ul li:hover {
  background-color: #f5f5f5;
}

.sidebar-content ul li p{
  margin: 0 10px;
}

/* 遮罩层动画 */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

/* 右侧导航栏动画 */
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.3s ease;
}

.slide-enter-from,
.slide-leave-to {
  transform: translateX(100%);
}
</style>
